import { useEffect, useRef, useState } from 'react'
import { Link } from 'react-router-dom'
import { Me } from '../../assets/Icons'
import Block from '../../components/Block/Block'
import SelectCar from '../../components/SelectCar/SelectCar'
import SelectPrice from '../../components/SelectPrice/SelectPrice'
import SelectState from '../../components/SelectState/SelectState'
import './DriverRoom.scss'
import request from '../../services/http'

const DriverRoom = () => {
   const [burger, setBurger] = useState(true)
   const [from, setFrom] = useState('1')
   const [to, setTo] = useState('2')
   const [car, setCar] = useState('1')
   const [price, setPrice] = useState('0')
   const [date, setDate] = useState()
   const dateRef = useRef()
   const [hasData, setHasData] = useState({})
   const [classifieds, setClassifieds] = useState([])


   useEffect(() => {
      let isCancelled = false;

      async function getQuestions() {
         try {
            const { data } = await request.post(`/driver`, {
               token: window.localStorage.getItem('token')
            });
            if (data) {
               setTo(data.driver.to_id)
               setFrom(data.driver.from_id)
               setCar(data.driver.car_id)
               setPrice(data.driver.price)
               setDate(data.driver.is_hiring)
               setClassifieds(data.classifieds)
               setHasData(data.driver)
               let togle = document.querySelector('#toggle')
               if (data.driver.is_hiring == 1 ) {
                  togle.checked = true
                  
               } else if (data.driver.is_hiring == 0 || data.driver.is_hiring.length > 1) {
                  togle.checked = false
               }
            }
         } catch (err) { }
      }
      !isCancelled && getQuestions();
   }, [date])

   const onChangeSubmit = (e) => {
      if (e.target.checked) {
         setDate('1')
         try {
            const { data } = request.post(`/work`, {
               token: window.localStorage.getItem('token'),
               work: '1'
            });
            if (data) {
            }
         } catch (err) {
            console.log(err);
         }
      } else {
         setDate('0')
         try {
            const { data } = request.post(`/work`, {
               token: window.localStorage.getItem('token'),
               work: '0'
            });
            if (data) {
               
            }
         } catch (err) {
            console.log(err);
         }
      }
      
   }


   const configButton = (e) => {
      e.preventDefault();
      if(from == to) {
         alert('Bir hil shaxarni tanlamang !')
      }else {
         request.post('/config', {
            token: window.localStorage.getItem('token'),
            from: from,
            to: to,
            car: car,
            price: price,
            date: dateRef.current.value

         })
            .then((response) => {
               if (response.data.message != 'error') {
                  if (response.data.message.is_hiring != '1' || response.data.message.is_hiring != '0') {
                     let togle = document.querySelector('#toggle')
                     togle.checked = false
                     setDate(response.data.message.is_hiring)
                     window.location.reload();
                  }
               }

            })
            .catch((err) => {
               console.log(err);
            })
      }

   }

   
   


   return (
      <div className="driverRoom">
         <div className="driverRoom-header">
            <div className="driverRoom-header-up">
               <div className={`burger ${burger ? 'active' : ''}`}
                  onClick={() => setBurger(!burger)}>
                  <span></span>
               </div>

               <Link to="/settings"><Me /></Link>

               <div className="togle">
                  <input id="toggle" type="checkbox" name='work' onChange={onChangeSubmit}/>
               </div>
            </div>
            <form onSubmit={configButton} className={`driverRoom-header-body ${burger ? 'active' : ''}`}>
               <div className="row">
                  <SelectState setFrom={setFrom} hasData={hasData.from_id} text="Qayerdan"/>
                  <SelectState setFrom={setTo} hasData={hasData.to_id} text="Qayerga"/>
               </div>
               <div className="row">
                  <SelectCar setCar={setCar} hasData={hasData.car_id}/>
                  <SelectPrice setPrice={setPrice} hasData={hasData.price}/>
               </div>
               <div className="row3">
                  <input type="date" id="date" ref={dateRef} defaultValue={date}/>
               </div>

               <button type="submit">Saqlash</button>

            </form>
         </div>

         <div className="driverRoom-body">
            {
               classifieds.length ? (
                  classifieds.map(el => {
                   return  <Block
                        car={el.car}
                        price={el.price}
                        driver={el.full_name}
                        phone={el.phone_number}
                     />
                  })
               ) : (
                  <></>
               )
            }
         </div>
      </div>
   )
}

export default DriverRoom