import './Info.scss'
import { InfoImg1 } from '../../assets/Icons'

const Info = ({nav, setNav}) => {
   return(
      <div className={`info ${nav ? 'active' : ''}`}>
         <div className="info-image">
            <InfoImg1/>
         </div>

         <div className="info-text">
            <h2 className="info-text-title">QULAY VA TEZKOR</h2>

            <h4 className="info-text-sub-title">Endi uydan chiqmasdan Mashina topish Biz bilan onson</h4>

            <button onClick={() => setNav(false)}>Keyingi</button>
         </div>
      </div>
   )
}

export default Info