import './About.scss'
import { Link } from 'react-router-dom'
import { Arrow } from '../../assets/Icons'
import { Me } from '../../assets/images'
import AboutBlock from '../../components/AboutBlock/AboutBlock'

const About = () => {
   return (
      <div className="about">
         <div className="about-header">
            <Link to="/change"><Arrow /></Link>
         </div>

         <div className="about-logo">
            <h1><span>On</span>line-Pitak</h1>
         </div>

         <div className="about-info">
            <h2>Bizning maqsadimiz*</h2>

            <p>
               <span>Yo'lovchilar uchun:</span><br />
               Sizga uydan chiqmasdan Mashina topish imkoniyatini berish. Sizning vaqtingizni va pulingizni tejashga yordam berish.
               <br />
               <br />
               <span>Haydovchilar uchun:</span><br />
               Endi siz ko'chada odam izlamaysiz, ular sizni o'zlari topishadi. 
            </p>
         </div>

         <div className="about-us">
            <h2>Asoschilar:</h2>

            <AboutBlock
               imgUser={Me}
               text1="Asadulloh"
               text2="Sadirdinov"
               info="Full-stack developer"
               link="https://www.youtube.com/channel/UCTtCJDh3ETDEvXXH_u0IJzg"
            />
            <AboutBlock
               imgUser={'https://icons.iconarchive.com/icons/icons8/windows-8/512/Users-User-icon.png'}
               text1="Qodirjon"
               text2="Odilov"
               info="UI-UX designer"
               link="https://qodir21169@gmail.com"
            />
         </div>


      </div>
   )
}


export default About