import './Settings.scss'
import {User, Arrow} from '../../assets/Icons'
import { Link } from 'react-router-dom'
import { useEffect, useState } from 'react'
import request from '../../services/http'

const Settings = () => {
   const [hasData, setHasData] = useState({})
   useEffect(() => {
      let isCancelled = false;

      async function getQuestions() {
         try {
            const { data } = await request.post(`/settings`, {
               token: window.localStorage.getItem('token')
            });
            if (data.message) {
               setHasData(data.message)
            }
         } catch (err) { }
      }
      !isCancelled && getQuestions();
   }, [])
   return(
      <div className="settings container">
         <div className="settings-header">
            <Link to="/driverRoom"><Arrow /></Link>
         </div>
         <div className="settings-logo">
            <User />
         </div>

         <form action="" className="settings-form">
            <input type="text" placeholder="Ism" value={hasData.firstname}/>
            <input type="text" placeholder="Familiya"  value={hasData.lastname}/>
         </form>


         <div className="button-box">
            <a href="tel:+998943821261">Aloqa</a>
            <button onClick={() => {
               window.localStorage.removeItem('token')
               window.location = '/change'
            }}>Chiqish</button>
         </div>
      </div>
   )
}

export default Settings