import './SelectPriceClient.scss'
import { toUniqueArray } from '../../lib/mufunc' 

const SelectPriceClient = ({setPrice, hasDate}) => {
   const changeFunc = (e) => {
      setPrice(e.target.value)
   }

   let array = []

  hasDate.map(el => {
      array.push(el.price)
   })
    array = toUniqueArray(array).sort()

   return(
      <select name="" id="" onChange={changeFunc} className={`priceClient ${hasDate.length ? 'active' : ''}`}>
         <option selected disabled>Narx</option>
         {
            array.length ? (
               array.map(el => {
                  return <option value={el}>{new Intl.NumberFormat().format(el)}</option>
               })
            ) : (
               <></>
            )
         }
      </select>
   )
}

export default SelectPriceClient