import './VerifyNext.scss'
import { Arrow, WhitePhone } from '../../assets/Icons'
import { useEffect, useRef } from 'react'
import { Link } from 'react-router-dom'
import request from '../../services/http'

const VerifyNext = () => {

   const one = useRef()
   const two = useRef()
   const three = useRef()
   const four = useRef()

   const handleSubmit = (e) => {
      
      e.preventDefault();
      request.post('/verify', {
         token: window.localStorage.getItem('token'),
         userCode: `${one.current.value}` + `${two.current.value}` + `${three.current.value}` + `${four.current.value}`,
      })
      .then((response) => {
         if (response.data.message != 'error') {
            window.localStorage.setItem('token', response.data.message)
            window.location = '/formDriver'
         }else if(response.data.message == 'error') {
            alert('SMS kod hato terilgan !')
         }
         
         })
         .catch((err) => {
            console.log(err);
         })

   }


   useEffect(() => {
      let inputEl = document.querySelector('.input')
      inputEl.focus()
   }, [])

   const nextFunc = (e) => {
      if(e.target.value.length > 1) {
         alert('Ko`p raqam kiritmang !')
         let inputs = e.target.parentNode.childNodes
         inputs.forEach(el => {
            el.disabled = false
            el.value = ''
         })
         inputs[0].focus()
      }
      else if (e.target.value.length > 0) {
         e.target.nextSibling.focus()
         e.target.disabled = true
      } else if (e.keyCode == 8) {
         let inputs = e.target.parentNode.childNodes
         inputs.forEach(el => {
            el.disabled = false
            el.value = ''
            
         })
         inputs[0].focus()
      }
   }


   return (
      <div className="verifyNext container">
         <div className="verifyNext-header">
            <Link to="/verify"><Arrow /></Link>
         </div>
         <div className="verifyNext-logo">
            <h1><span>On</span>line-Pitak</h1>
         </div>

         <form onSubmit={handleSubmit} className="verifyNext-form">
            <label htmlFor="">SMS Parolni tering</label>
            <div className="row">
               <input type="number"
                  onKeyUp={nextFunc}
                  ref={one}
                  minLength="1" maxLength="1"  className="input"/>
               <input type="number"
                  onKeyUp={nextFunc}
                  ref={two}
                  minLength="1" maxLength="1" />
               <input type="number"
                  onKeyUp={nextFunc}
                  ref={three}
                  minLength="1" maxLength="1" />
               <input type="number"
                  ref={four}
                  onKeyUp={(e) => {
                     if (e.keyCode == 8) {
                        let inputs = e.target.parentNode.childNodes
                        inputs.forEach(el => {
                           el.disabled = false
                           el.value = ''
                        })
                        inputs[0].focus()
                     }else if (e.target.value.length > 1) {
                        alert('OOO qara harom')
                        let inputs = e.target.parentNode.childNodes
                        inputs.forEach(el => {
                           el.disabled = false
                           el.value = ''
                        })
                        inputs[0].focus()
                     }
                  }}
               minLength="1" maxLength="1" />
            </div>
            <button type="submit" >Kengisi</button>
         </form>
      </div>
   )
}

export default VerifyNext