import { User, Arrow } from '../../assets/Icons'
import './Login.scss'
import { Link } from 'react-router-dom'
import { useRef } from 'react'
import request from '../../services/http'

const Login = () => {
   const phone = useRef('')
   const password = useRef('')

   if(window.localStorage.getItem('token')) {
      window.location = '/driverRoom'
   }

   const handleSubmit = (e) => {
      e.preventDefault()
      request.post('/login', {
          phone_number: phone.current.value,
          password: password.current.value 
         }).then((response) => {
            if (response.data.message != 'error') {
               window.localStorage.setItem('token', response.data.message);
               window.location = '/driverRoom'
            }else {
               alert('Nomer yoki parol noto`g`ri terilgan')
            }

         })
         .catch((err) => {
            console.log(err);
         })

   }
   return (
      <div className="Login container">
         <div className="Login-header">
            <Link to="/change"><Arrow /></Link>
         </div>
         <div className="Login-logo">
            <User />
         </div>

         <form onSubmit={handleSubmit} className="Login-form">
            <input type="tel" placeholder="998941234567" ref={phone} minLength={12} maxLength={12}/>
            <input type="password" placeholder="Parol" ref={password} />
            <button type="submit">Kirish</button>
            <Link to="/verify">Registratsiya</Link>
         </form>
      </div>
   )
}

export default Login