import './Verify.scss'
import { Arrow, WhitePhone } from '../../assets/Icons'
import { Link } from 'react-router-dom'
import { useRef } from 'react'
import request from '../../services/http'

const Verify = () => {
   const number = useRef('')



   const handleSubmit = (e) => {
    
      e.preventDefault();
      request.post('/register', {
         usernumber: number.current.value,
      })
         .then((response) => {
            if(response.data.message != 'error') {
               window.localStorage.setItem('token', response.data.message)
               window.location = '/verifyNext'
            }

         })
         .catch((err) => {
            console.log(err);
         })

   }


return (
   <div className="verify container">
      <div className="verify-header">
         <Link to="/login"><Arrow /></Link>
      </div>
      <div className="verify-logo">
         <h1><span>On</span>line-Pitak</h1>
      </div>

      <form className="verify-form" onSubmit={handleSubmit}>
         <label htmlFor="">Telefon raqamingizni tering</label>
         <div className="row">
            <WhitePhone /> <input type="tel" placeholder="998941234567" minLength={12} maxLength={12}ref={number} />
         </div>
         <button type="submit" >Kengisi</button>
      </form>
   </div>
)}


export default Verify