import './assets/styles/main.scss'
import { useEffect, useRef, useState } from "react";
import request from "./services/http";
import Verify from './containers/Verify/Verify';
import VerifyNext from './containers/VerifyNext/VerifyNext';
import {
  Change,
  Home
} from './pages'
import {
  BrowserRouter as Router,
  Route,
  Switch
} from 'react-router-dom'
import FormDriver from './containers/FormDriver/FormDriver';
import DriverRoom from './containers/DriverRoom/DriverRoom';
import Settings from './containers/Settings/Settings';
import Login from './containers/Login/Login';
import ClientRoom from './containers/ClientRoom/ClientRoom';
import About from './containers/About/About';
const App = () => {
  const [token, setToken] = useState(window.localStorage.getItem('token'))


  return (
    <div className="app">
      <Router>
        {
          token ? (
            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/verify" component={Verify} />
              <Route path="/verifyNext" component={VerifyNext} />
              <Route path="/change" component={Change} />
              <Route path="/formDriver" component={FormDriver} />
              <Route path="/driverRoom" component={DriverRoom} />
              <Route path="/client" component={ClientRoom} />
              <Route path="/settings" component={Settings} />
              <Route path="/login" component={Login} />
              <Route path="/about" component={About} />
            </Switch>
          ) : (
            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/verify" component={Verify} />
              <Route path="/verifyNext" component={VerifyNext} />
              <Route path="/change" component={Change} />
              <Route path="/formDriver" component={FormDriver} />
              <Route path="/client" component={ClientRoom} />
              <Route path="/login" component={Login} />
              <Route path="/about" component={About} />
            </Switch>
          )
        }
      </Router>
    </div>
  );
}

export default App;
