import './Info2.scss'
import { InfoImg2 } from '../../assets/Icons'
import { Link } from 'react-router-dom'

const Info2 = ({ nav, setNav }) => {
   return (
      <div className={`info ${nav ? '' : 'active'}`}>
         <div className="info-image">
            <InfoImg2 />
         </div>

         <div className="info-text">
            <h2 className="info-text-title">PULNI TEJASH</h2>

            <h4 className="info-text-sub-title">Vaqt va pulingizni tejang.  Haydovchilardan narx va vaqt uchun eng yaxshisini tanlang.</h4>

            <Link to="/change">Keyingi</Link>
         </div>
      </div>
   )
}

export default Info2