import { Phone } from '../../assets/Icons'
import './Block.scss'

const Block = ({car, phone, driver, price}) => {

   price = new Intl.NumberFormat().format(price)
   return(
      <div className="block">
         <div className="block-row">
            <h3>{car}</h3>
            <a href={`tel:+${phone}`}><Phone /><span>+{phone}</span></a>
         </div>
         <div className="block-row">
            <h5>{driver}</h5>
            <h4>{price} uzs</h4>
         </div>
      </div>
   )
}

export default Block