import './SelectPrice.scss'

const SelectPrice = ({setPrice, hasData}) => {
      const changeFunc = (e) => {
            setPrice(e.target.value);
      }
      let str = hasData

      if(hasData) {
            str = str.substring(0, hasData.length - 3)
      }

      
   return(
         <input type="number" placeholder="Narx" onKeyUp={changeFunc} defaultValue={str} required/>
   )
}

export default SelectPrice