import { useState, useEffect } from 'react';
import './SelectState.scss'
import Loader from '../Loader/Loader';
import request from '../../services/http';

const SelectState = ({ setFrom, hasData, text }) => {
   const [states, setStates] = useState([])
   const [wait, setWait] = useState(false)
   const changeFunc = (e) => {
      setFrom(e.target.value);
   }
   useEffect(() => {
      let isCancelled = false;
      setWait(true)

      async function getQuestions() {
         try {
            const { data } = await request.get(`/state`);
            if (data) {
               setStates(data)
               setWait(false)
            }
         } catch (err) { }
      }
      !isCancelled && getQuestions();
   }, [])


   return (
      <>
         {
            wait ? (
               <Loader />
            ) : (
               <select name="" id="" onChange={changeFunc}>
                     <option selected disabled>{text}</option>

                  {
                     states.length ? (
                        states.map(el => {
                           if (el.id == hasData) {
                              return <option value={el.id} selected>{el.name}</option>
                           } else {
                              return <option value={el.id}>{el.name}</option>
                           }
                        })
                     ) : (
                        <></>
                     )
                  }
               </select>
            )
         }

      </>
   )
}

export default SelectState