function money(x) {
   return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ");
}

function toUniqueArray(a) {
   var newArr = [];
   for (var i = 0; i < a.length; i++) {
      if (newArr.indexOf(a[i]) === -1) {
         newArr.push(a[i]);
      }
   }
   return newArr;
}


export {
   money,
   toUniqueArray
}