import './AboutBlock.scss'

const AboutBlock = ({imgUser, text1, text2, info, link}) => {
   return(
      <a href={`${link}`} className="aboutBlock">
         <div className="aboutBlock-image">
            <img src={imgUser} alt="" />
         </div>

         <div className="aboutBlock-info">
            <h3>{text1}</h3>
            <h3>{text2}</h3>

            <h4>{info}</h4>
         </div>
      </a>
   )
}

export default AboutBlock