import { User, Arrow } from '../../assets/Icons'
import './FormDriver.scss'
import { Link } from 'react-router-dom'
import request from '../../services/http'
import { useRef } from 'react'

const FormDriver = () => {

   const userName = useRef('')
   const userSurname = useRef('')
   const userPasword = useRef('')
   const handleSubmit = (e) => {
      e.preventDefault();
      request.post('/form', {
         token: window.localStorage.getItem('token'),
         userName: userName.current.value,
         userSurname: userSurname.current.value,
         password: userPasword.current.value
      })
         .then((response) => {
            if (response.data.message != 'error') {
               window.localStorage.setItem('token', response.data.message)
               window.location = '/driverRoom'
            }

         })
         .catch((err) => {
            console.log(err);
         })

   }
   return(
      <div className="formDriver container">
         <div className="formDriver-logo">
            <User/>
         </div>

         <form onSubmit={handleSubmit} className="formDriver-form">
            <input type="text" placeholder="Ism" ref={userName} required/>
            <input type="text" placeholder="Familiya" ref={userSurname} required/>
            <input type="password" placeholder="Parol" ref={userPasword} required/>
            <button type="submit">Saqlash</button>
         </form>
      </div>
   )
}

export default FormDriver