import { useState, useEffect, useRef } from 'react'
import request from '../../services/http'
import { Link } from 'react-router-dom'
import { Arrow } from '../../assets/Icons'
import Block from '../../components/Block/Block'
import SelectCar from '../../components/SelectCar/SelectCar'
import SelectPriceClient from '../../components/SelectPriceClient/SelectPriceClient'
import SelectState from '../../components/SelectState/SelectState'
import './ClientRoom.scss'
import SelectDateClient from '../../components/SelectDateClient/SelectDateClient'
import Loader from '../../components/Loader/Loader'

const ClientRoom = () => {
   const [burger, setBurger] = useState(true)
   const [hasDate, setHasDate] = useState([])
   const [prices, setPrices] = useState([])
   const [from, setFrom] = useState(12)
   const [to, setTo] = useState(1)
   const [car, setCar] = useState(1)
   const [price, setPrice] = useState()
   const [wait, setWait] = useState(false)
   // const [date, setDate] = useState()

   

   const configButton = (e) => {
      setWait(true)
      e.preventDefault();
      if (from == to) {
         alert('Bir hil shaxarni tanlamang !')
      } else {
         request.post('/classified', {
            fromId: from,
            toId: to,
            carId: car

         })
            .then((response) => {
               if (response.data) {
                  setHasDate(response.data)
                  setPrices(response.data)
                  setWait(false)
               }
            })
            .catch((err) => {
               console.log(err);
            })
      }

   }

   useEffect(() => {
      let array = []
      hasDate.length ? (
         hasDate.map((el, index) => {
            if (el.price == price) {
               array.push(el)
            }
         })
      ) : (
         <></>
      )
      setPrices(array)
   }, [price])

   // useEffect(() => {
   //    let array = []
   //    hasDate.length ? (
   //       hasDate.map((el, index) => {
   //          if (el.available == date) {
   //             array.push(el)
   //          }
   //       })
   //    ) : (
   //       <></>
   //    )
   //    setPrices(array)
   // }, [date])


   return (
      <div className="ClientRoom">
         {
            wait ? (
               <Loader />
            ) : (
               <></>
            )
         }
         <div className="ClientRoom-header">
            <div className="ClientRoom-header-up">
               <div className="verifyNext-header">
                  <Link to="/change"><Arrow /></Link>
               </div>

               <div className={`burger ${burger ? 'active' : ''}`}
                  onClick={() => setBurger(!burger)}>
                  <span></span>
               </div>
            </div>
            <form onSubmit={configButton} className={`ClientRoom-header-body ${burger ? 'active' : ''}`}>
               <div className="row">
                  <SelectState setFrom={setFrom} text="Qayerdan"/>
                  <SelectState setFrom={setTo} text="Qayerga"/>
               </div>
               <div className="row">
                  <SelectCar setCar={setCar}/>
                  <SelectPriceClient setPrice={setPrice} hasDate={hasDate}/>
               </div>
               <div className="row3">
                  {/* <SelectDateClient hasDate={hasDate} setDate={setDate}/> */}
               </div>

               <button type="submit">Qidirish</button>

            </form>
         </div>

         <div className="ClientRoom-body">
            {
               prices.length ? (
                  prices.map(el => {
                    return <Block
                        car={el.car}
                        phone={el.phone_number}
                        driver={el.full_name}
                        price={el.price}
                    />
                  })
               ) : (
                  <></>
               )
            }
         </div>
      </div>
   )
}

export default ClientRoom